:root {
  --main-color: #00acdc;
}

body {
  box-sizing: border-box;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .5s, border-color .5s;
  display: inline-block;
}

.nav__link:hover {
  color: var(--main-color);
}

a.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
  bottom: 0;
  left: 0;
}
